p>span{display:block;color:#f5f5f5}p>span:nth-child(1){font-size:18px;color:var(--primary-color);font-weight:200}p>span:nth-child(1) span{display:inline;font-weight:100;font-style:italic}p>span:nth-child(2){font-size:14px}p>span:nth-child(3){font-size:18px}p>span:nth-child(3) span{display:inline-block}p>span:nth-child(3) span::first-letter{color:var(--primary-color)}

.searchbox-container{width:100%;position:relative}.searchbox-container .search-loader{display:inline-block;height:11vmin;position:absolute;top:50%;right:0;transform:translateY(-50%)}.searchbox-container .search-loader svg{height:11vmin;width:auto}.searchbox{width:100%;height:12vmin;border-radius:100vmax;font-size:4vmin;padding:0 6vmin;outline:none;border:1px solid #e0e0e0;box-shadow:0 5px 5px rgba(0,0,0,0.25),inset 0 5px 5px rgba(0,0,0,0.25);transition:all 100ms}.searchbox::-webkit-input-placeholder{text-align:center;color:#e5e5e5}.searchbox:-ms-input-placeholder{text-align:center;color:#e5e5e5}.searchbox::-ms-input-placeholder{text-align:center;color:#e5e5e5}.searchbox::placeholder{text-align:center;color:#e5e5e5}.searchbox:focus{border:2px #FF7324 solid;transform:scaleX(1.01) scaleY(1.05);box-shadow:0 7px 5px rgba(0,0,0,0.25),inset 0 7px 5px rgba(0,0,0,0.25)}

.home{min-height:100%;display:flex;flex-direction:column;align-items:center;position:relative;overflow:hidden;padding:0 10px}.home .search-error{color:rgba(0,0,0,0.5);font-size:2em}

div.logo{margin:0 0 20px 0;font-family:Lobster Two;font-style:normal;font-weight:normal;font-size:48px;line-height:60px;color:var(--primary-color);align-self:flex-start}div.logo.mob{font-size:24px}

.listItem{display:flex;justify-content:space-between}.listItem__leadimg{width:15vw;height:10vh}.listItem__leadimg__img{max-width:100%;max-height:100%;width:auto;height:auto}.listItem__text{margin:0 0 0 1rem;flex-grow:1;max-width:75%}.listItem__text h1,.listItem__text h3{margin:0;text-align:left}.listItem__text h1{font-size:x-large;color:var(--primary-color);font-weight:500;letter-spacing:0.07em}.listItem__text h3{color:whitesmoke;font-weight:400;letter-spacing:0.05em;text-transform:capitalize}

.search-suggestion{width:90%;margin:1.5em auto 0 auto;border-radius:.7em}.search-suggestion .list{padding:0}.search-suggestion .list .list__list-item{list-style:none}.search-suggestion .list .list__list-item .listItem{margin:.8em 0;background-color:rgba(0,0,0,0.3);padding:.7em;border-radius:.7em}

.App{text-align:center;padding:5vh 5vw;height:100%}

